.ScoreBox {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 30%;
}

.ScoreBox * {
	margin-bottom: 5px;
}

.ScoreBox div {
	text-transform: uppercase;
	font-size: 0.8rem;
	letter-spacing: 0.005rem;
}

.ScoreBox-score-bar {
	height: 10px;
	width: 100%;
	transition: width 0.5s;
}
@media (min-width: 575px) {
	.ScoreBox div {
		font-size: 1rem;
	}
}
